header {
    background-color: #141416;
    display: flex;
    justify-content: flex-end;
    padding: 1rem;
    margin-bottom: 2rem;
  }

  #smooth {
      padding-right: 20px;
      width: 200px;
  }

  #smooth form {
      position: relative;
      height: 5vh;
      max-width: 100%;
      width: 100%;
      background: #fff;
      padding-right: 125px;
      border-radius: 10px;
  }

  #formA {
      padding: 0;
  }

  #smooth form::before,
  #smooth form:after {
      content: "";
      position: absolute;
      inset: 0;
      background: #f00;
      transition: .5s;
      border-radius: 10px;
  }

  #smooth form:nth-child(1)::before,
  #smooth form:nth-child(1)::after {
      background: linear-gradient(45deg, #00ccff,#0e1538,#0e1538,#d400d4); 
  }

  #smooth form:hover::before {
      inset: -3px;
  }

  #smooth form:hover::after {
      inset: -3px;
      filter: blur(10px);
  }

  #smooth form input {
    position: absolute;
    width: 100%;
    height: 102%;
    background: #0e1538;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2em;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #fff;
    border: 1px solid #040a29;
    border-radius: 10px;
    overflow: hidden;
    top: 0;
    left: 0;
  }

  #smooth form input::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.75);
      transform: skew(25deg);
  }

  .search::placeholder {
      color: rgb(195, 228, 50);
      text-align: center;
  }

  .navbar {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #141416;
      width: 100%;
  }

  .navbar-links {
      max-width: 100%;
  }

  .navbar-links ul {
      margin: 0;
      padding: 0;
      display: flex;
      color: #fff;
  }

  .navbar-links li {
      list-style: none;
      margin: auto;
  }

  .navbar-links li a {
      color: #fff;
      text-decoration: none;
      padding: 1rem;
      display: block;
      transform: .3s ease;
  }

  .navbar-links a:hover {
      color: rgb(195, 228, 50);
      transform: scale(1.08);
  }

  .toggle-button {
      position: absolute;
      top: .75rem;
      right: 1rem;
      display: none;
      flex-direction: column;
      justify-content: space-between;
      width: 30px;
      height: 21px;
      background-color: #fff;
  }

  .toggle-button .bar {
      height: 3px;
      width: 100%;
      background-color: black;
      border-radius: 10px;
  }

  .stick {
      position: fixed;
      top: 0;
      width: 100%;
      z-index: 1;
  }


  @media (max-width: 700px) {
    .toggle-button {
        display: flex;
        margin-top: 1rem;
      }
      
      .navbar-links {
        display: none;
        width: 100%;
      }
      
      .navbar {
        flex-direction: column;
        align-items: flex-start;
      }

      .input {
        width: 50%;   
        border-radius: 10px;
      }
      
      .navbar-links ul {
        width: 100%;
        flex-direction: column;
      }
      
      .navbar-links li {
        text-align: center;
      }
      
      .navbar-links li a {
        padding: .5rem 1rem;
      }
      
      .navbar-links.active {
        display: flex;
      }

      #smooth {
          width: 50%;
          padding: 0;
          margin-top: 10px;
      }
  }